* {
  margin: 0;
  padding: 0;
}

body {
  color: #000;
  --header-height: 60px;
  --link-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 25px;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  vertical-align: middle;
  width: 40px;
  height: 40px;
}

.nav__list {
  display: flex;
}

.menu__item {
  margin-right: 20px;
  list-style: none;
  position: relative;
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__link {
  text-transform: uppercase;
  text-decoration: none;
}

.menu__item a {
  line-height: var(--header-height);
  display: inline-block;
}

.menu__item a:hover, a.is-active {
  color: var(--link-color);
}

a.is-active:after {
  content: "";
  background-color: var(--link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
